.App {
  /* text-align: center; */
  width: 450px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  /* font-family: "Montserrat", sans-serif; */
  font-family: "Roboto";
}

.header {
  display: flex;
  color: #fff;
  margin-bottom: 20px;
  padding-left: 20px;
  background-color: #E70074;
  height: 80px;
  align-items: center;
}

.delivery {
  width: 400px;
  height: 110px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  padding-top: 5px;
  padding-left: 20px;
  box-shadow: 1px 1px 10px #88888885;
  border-radius: 5px;
  cursor: pointer;
}

.delivery h4 {
  color: #D7D7D7;
}

.header-title {
  /* display: inline-block; */
  margin: 4px 0 0 12px;
  font-size: 1.4em;
}

.order {
  height: 290px;
}

.variant h5 {
  width: 140px;
  height: 30px;
  background-color: #EFEFEF;
  padding-top: 14px;
  padding-left: 8px;
  border-radius: 10px;
  margin-top: 7px;
  margin-bottom: 7px;
}

.order p {
  margin: 0;
}

.order p .piece {
  color: #929292;
}

.order p .price {
  color: #E70074;
}

#standard-basic {
  width: 380px;
}

.items {
  display: flex;
  font-weight: 500;
}

.right-panel {
  margin-left: 40%;
}

.total {
  color: #E70074;
}

.finish {
  margin-left: 15px;
  font-weight: bold;
}
